import React, { Component } from "react";
import PropTypes from "prop-types";
import { Modal, ModalBody, Input } from "reactstrap";
import Tag from "../../../components/Tag.js";

const propTypes = {
  modal: PropTypes.bool,
  close: PropTypes.func,
  data: PropTypes.array
};

const TAG_LIST = [
  ["Normal", "#ccc", "#000"],
  ["Common", "#ccc", "#000"],
  ["Rare", "#9fe8ff", "#000"],
  ["Elite", "#c4adff", "#000"],
  ["Super Rare", "#ee9", "#000"],
  ["Ultra Rare", "linear-gradient(120deg,#fbffca,#baffbf,#a7efff,#ffabff)", "#000"],
  ["Priority", "#ee9", "#000"],
  ["Decisive", "linear-gradient(120deg,#fbffca,#baffbf,#a7efff,#ffabff)", "#000"],
  ["Research", "#e99", "#000"],
  ["Retrofit", "#e99", "#000"],
  ["Vanguard", "#9a9", "#000"],
  ["Main", "#9a9", "#000"],
  ["Destroyer", "#9a9", "#000"],
  ["Cruiser", "#9a9", "#000"],
  ["Light Cruiser", "#9a9", "#000"],
  ["Heavy Cruiser", "#9a9", "#000"],
  ["Large Cruiser", "#9a9", "#000"],
  ["Battleship", "#9a9", "#000"],
  ["Aviation Battleship", "#9a9", "#000"],
  ["Monitor", "#9a9", "#000"],
  ["Carrier", "#9a9", "#000"],
  ["Aircraft Carrier", "#9a9", "#000"],
  ["Light Carrier", "#9a9", "#000"],
  ["Repair", "#9a9", "#000"],
  ["Munition", "#9a9", "#000"],
  ["Submarine", "#9a9", "#000"],
  ["Eagle Union", "#9e9", "#000"],
  ["Royal Navy", "#9e9", "#000"],
  ["Sakura Empire", "#9e9", "#000"],
  ["Iron Blood", "#9e9", "#000"],
  ["Dragon Empery", "#9e9", "#000"],
  ["Sardegna Empire", "#9e9", "#000"],
  ["Northern Parliament", "#9e9", "#000"],
  ["Iris Libre", "#9e9", "#000"],
  ["Vichya Dominion", "#9e9", "#000"],
  ["META", "#9e9", "#000"],
  ["Universal", "#9e9", "#000"],
  ["Collab", "#9e9", "#000"],
  ["MGM1", "#999", "#000"],
  ["MGM2", "#999", "#000"],
  ["MGM3", "#999", "#000"],
  ["DDAUX", "#999", "#000"],
  ["DDAOA", "#999", "#000"],
  ["DDTRP", "#999", "#000"],
  ["Fighter", "#999", "#000"],
  ["Dive Bomber", "#999", "#000"],
  ["Torpedo Bomber", "#999", "#000"],
  ["Anti Air", "#999", "#000"],
  ["Torpedo", "#999", "#000"],
  ["Heal", "#999", "#000"],
  ["Preload", "#999", "#000"],
  ["Buff", "#999", "#000"],
  ["Debuff", "#999", "#000"]
];

export default class ShipModal extends Component
{
  constructor(props)
  {
    super(props);
    this.state = {
      tagList: [],
      search: ""
    };
  };

  componentDidUpdate(prevProps, prevState)
  {
    if (prevState.search !== this.state.search)
      this.searchResult();
  };

  search = (event) => this.setState({ search: event.target.value });

  searchResult = () =>
  {
    let current_state = this.state.search.toLowerCase();
    let current_tag = this.state.tagList;

    TAG_LIST.forEach((idx) =>
    {
      const TAG_NAME = idx[0].toLowerCase();
      if (this.state.search.includes(TAG_NAME))
      {
        current_state = current_state.replace(TAG_NAME, "");
        if (!current_tag.includes(TAG_NAME))
        {
          current_tag.push(TAG_NAME);
          document.getElementById("bast").innerHTML = current_tag;
        };
        document.getElementById("search_html").value = current_state;
      };
    });

    this.setState({ search: current_state, tagList: current_tag });
  };

  // conclusion tag search is only available for rarity + research + retrofit
  render()
  {
    const MODAL = this.props.modal;
    const CLOSE = this.props.close;
    const DATA  = this.props.data;

  	return (
      <Modal isOpen={MODAL} toggle={CLOSE} size="xl" className="pb-5 dark">
        <div className="modal-header dis-inlineblock text-center align-middle">
          <h1 className="dis-inlineblock">
            <i className="tim-icons icon-tag tim-icons-32"/>
            <br/>Tag List
          </h1>
          <button
            type="button"
            className="close modal-close"
            data-dismiss="modal"
            aria-hidden="true"
            onClick={CLOSE}
          >
            <i className="tim-icons icon-simple-remove font30px" />
          </button>
        </div>
        <ModalBody>
          <Input
            id="search_html"
            type="search"
            placeholder="Search"
            onChange={this.search}
          />
          <div id="bast">container</div>
          <div id="bast">tag prototype testground</div>
          <div>
            {TAG_LIST.map((idx, key) =>
            {
              const TAG   = idx[0];
              const COLOR = idx[1];
              const FONT  = idx[2];
              return <Tag key={key} text={TAG} color={COLOR} font={FONT} />;
            })}
          </div>
          <div className="p-10"/>
        </ModalBody>
      </Modal>
    );
  }
};

ShipModal.propTypes = propTypes;
