import React, { Component } from "react";
import PropTypes from "prop-types";
import {
  Alert,
  Button,
  InputGroup,
  InputGroupText,
  Input
} from "reactstrap";

// Core Components
import Navbar     from "../../components/Navbar/Navbar.js";
import PageHeader from "../../components/PageHeader/PageHeader.js";
import Footer     from "../../components/Footer/Footer.js";
import ShipCard   from "./ShipList/ShipCard.js";
import ShipModal  from "./ShipList/ShipModal.js";

import shipSearch from "./ShipList/shipSearch.js";
const propTypes = {
  azurAPI: PropTypes.any,
  shipBanner: PropTypes.any
};

export default class ShipList extends Component
{
  constructor(props)
  {
    super(props);
    this.state = {
      azurapi: [],
      search: "",
      modal: false,
      modalData: [],
      focused: "",
      propsUpdate: false
    };
    this.timer = null;
  };

  componentDidUpdate(prevProps, prevState)
  {
    if (prevState.azurapi !== this.props.azurAPI && !this.state.propsUpdate)
      this.setState({ azurapi: this.props.azurAPI, propsUpdate: true });

    if (this.state.propsUpdate && prevState.search !== this.state.search)
      this.searchResult();
  };

  sortReset = () => this.setState({ azurapi: this.props.azurAPI });

  modalOpen = () => this.setState({ modal: true });
  modalClose = () => this.setState({ modal: false });

  onFocus = () => this.setState({ focused: "input-group-focus" });
  onBlur = () => this.setState({ focused: "" });

  search = (event) => this.setState({ search: event.target.value });

  searchResult = () =>
  {
    clearTimeout(this.timer);
    this.timer = setTimeout(() =>
    {
      const SEARCH = shipSearch(this.state.search, this.props.azurAPI);
      this.setState({ azurapi: SEARCH });
    }, 300);
  };

  sortById = () =>
  {
    this.setState(prevState => ({
      azurapi: [...prevState.azurapi].sort((a, b) => a.id.localeCompare(b.id))
    }));
  };

  sortByName = () =>
  {
    this.setState(prevState => ({
      azurapi: [...prevState.azurapi].sort((a, b) => a.names.en.localeCompare(b.names.en))
    }));
  };

  render()
  {
    const AZURAPI = this.state.azurapi;
    const BANNER  = this.props.shipBanner;

    return (
      <>
        <Navbar />
        <div className="wrapper">
          <PageHeader />
          <Alert color="warning" className="text-center align-middle">
            This page is under Development!
          </Alert>
          <div className="main text-center align-middle shipheader">
            <ShipModal data={this.state.modalData} close={this.modalClose} modal={this.state.modal}/>
            <div className="content">
              <h1>Ship List</h1>
              <Button outline color="danger" className="btn-icon" onClick={this.sortReset}>
                <i className="tim-icons icon-refresh-02"/>
              </Button>
              <Button outline color="success" className="btn-icon" onClick={this.modalOpen}>
                <i className="tim-icons icon-tag"/>
              </Button>
              <InputGroup className={this.state.focused}>
                <div className="input-group-prepend">
                  <InputGroupText><i className="tim-icons icon-zoom-split"/></InputGroupText>
                </div>
                <Input
                  type="search"
                  placeholder="Search"
                  onFocus={this.onFocus}
                  onBlur={this.onBlur}
                  onChange={this.search}
                />
              </InputGroup>
              <div className="containerCS">
                {AZURAPI.map((ship) => <ShipCard key={ship.id} ship={ship} banner={BANNER} />)}
              </div>
            </div>
          </div>
          <Footer />
        </div>
      </>
    );
  };
};

ShipList.propTypes = propTypes;
